const formatDate = (value) => {
  var d = new Date(value),
    mm = "" + (d.getMonth() + 1),
    dd = "" + d.getDate(),
    yy = d.getFullYear(),
    hh = "" + d.getHours(),
    ii = "" + d.getMinutes(),
    ss = "" + d.getSeconds();

  if (mm.length < 2) mm = "0" + mm;
  if (dd.length < 2) dd = "0" + dd;

  if (hh.length < 2) hh = "0" + hh;
  if (ii.length < 2) ii = "0" + ii;
  if (ss.length < 2) ss = "0" + ss;

  return [yy, mm, dd].join("-") + " " + [hh, ii, ss].join(":");
};
